.createInput{
    border:none;
    outline:none;
    box-shadow: none;
    width: 300px;
    height: 75px;
    font-size: 20px;
    resize: none;
    border-radius: 10px;
    padding:10px
}

.formContainer{
    display: flex;
    justify-content: center;
    align-items: center;

}

.submitButton{
    margin-left:10px;
    height: 50px;
    border:none;
    outline:none;
    border-radius: 10px;
    background-color: rgb(16, 16, 87);
    color:white;
}

.contenedorPadre{
    text-align: center;
}

.contenedorPadre p{
    margin-top:50px;
    
}

.contenedorPadre span{
    color:red;
}

.clearList{
    width: 150px;
    border:none;
    outline:none;
    padding:20px;
    border-radius: 10px;
    cursor: pointer;
    margin-right: 10px;
}