.cardNote{
    width: 200px;
    height: 150px;
    background-color: rgb(28, 7, 102);
    margin-left:20px;
    margin-top:20px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    color:white;
    position: relative;
}

.buttonDelete{
    background-color: rgb(248, 118, 118);
    color:white;
    border:none;
    outline:none;
    position: absolute;
    top:3px;
    right:3px;
    padding:10px;
    border-radius:10px;
    cursor: pointer;
}